import React, {Component} from 'react';
import {message,Row, Col,Icon, Timeline} from 'antd';
import Markdown  from 'react-markdown';




class About extends Component {
    constructor(props) {
        super(props);
        if (window.erred && window.erred > 3 && window.erred < 6) {
            message.error('');
            window.erred = window.erred + 1;
        } else if (window.erred && window.erred > 5) {
            window.location.replace("http://news.baidu.com/")
        }

        var oneDay = 24*60*60*1000;
        var start = new Date();
        this.state = {
            oneDay:oneDay ,
            start :start,

        }
    }





    render() {

        const {start,oneDay } = this.state;

        return (
            <div className="gutter-example">
                <Row gutter={8}>
                    <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                        <p>  <Icon type="mail" style={{marginRight: 8}}/>nick070809@163.com</p>
                        <p>  <Icon type="environment-o" style={{marginRight: 8}}/></p>
                        <p>  <Icon type="dashboard" style={{marginRight: 8}}/>java /python /scala /mysql /oracle
                            /react</p>
                        <p>  <Icon type="instagram" style={{marginRight: 8}}/>c++</p>
                        <p>&nbsp;</p>
                    </Col>

                    <Col xs={24} sm={24} md={3} lg={3} xl={3}></Col>


                    <Col xs={24} sm={24} md={9} lg={9} xl={9}>

                        <Timeline >
                            <Timeline.Item color="blue">2016-</Timeline.Item>
                            <Timeline.Item color="green">2012-</Timeline.Item>
                            <Timeline.Item color="green">2010-</Timeline.Item>
                            <Timeline.Item color="red">2006-</Timeline.Item>
                        </Timeline>
                    </Col>
                </Row>

                <p> 2019 11   { Math.ceil((new Date(2019,(11-1),11) -start)/oneDay) }  , 2020    { Math.ceil((new Date(2020,(1-1),25) -start)/oneDay) } </p>
                <p> 2020 11   { Math.ceil((new Date(2020,(11-1),11) -start)/oneDay) }  , 2021    { Math.ceil((new Date(2021,(2-1),11) -start)/oneDay) } </p>
                <p> 2021 11   { Math.ceil((new Date(2021,(11-1),11) -start)/oneDay) }  , 2022    { Math.ceil((new Date(2022,(2-1),1) -start)/oneDay) } </p>
                <p> 2022    { Math.ceil((new Date(2022,(9-1),10) -start)/oneDay) }  , 2022 11   { Math.ceil((new Date(2022,(11-1),11) -start)/oneDay) } </p>

                <p> 2019 1018   { Math.ceil((new Date(2019,(10-1),18) -start)/oneDay) }</p>


            </div>
        );
    }
}

export default About;


